import RaTe from "../../../components/uni-ui/uni-rate/uni-rate.vue"	
	 export default{
		 components:{
			RaTe 
		 },
		 data(){
			 return{
				 imgs:{
					 img1:require("../../../static/login/part5_picture2.png"),
					 img2:require("../../../static/order/order-evaluate/false.png"),
					 img3:require("../../../static/order/order-evaluate/img.png"),
					 img4:require("../../../static/order/order-evaluate/video.png"),
					 img5:require("../../../static/login/cirle.png"),
					 img6:require("../../../static/login/ok.png") 
				 },
				 color:'#f2f2f2',
				 activeColor:"#eb828f",
				 size:"30",
				 ss:"",
				 pjlist:[
					 {name:"差"},
					 {name:"一般"},
					 {name:"好"},
					 {name:"很好"},
					 {name:"非常好"},
				 ],
				evalcontent:"",
				imglist:[
					{
					 path:'',
					 imgtrue:false,
					 imgtext:"添加照片"	  
					}
				],
				imglists:"",
				tslength:0,
				ok:true,
				nmtitle:"你写的评价会以匿名的形式展现",
				src:"",
				srctrue:false,
				change:"非常好",
				change1:"",
				change2:"",
			 }
		 },
		 watch:{
			 
		 },
		 methods:{
			 chang($event){
			  var  e=$event;
			  var  a=this.watchvalue(e);
			  this.change=a;
	  
			},
			 chang1($event){
				 var  e=$event;
				 var  a=this.watchvalue(e);
				 this.change1=a;
			 },
			 chang2($event){
				 var  e=$event;
				 var  a=this.watchvalue(e);
				 this.change2=a;
			 },
			 watchvalue(e){
				 var title="";	 
				if(e==1){
									title="非常差" 
				}else if(e==2){
									 title="很差"
									 }else if(e==3){
									 title="一般"
									 }else if(e==4){
									 title="很好"
									 }else if(e==5){
									 title="非常好"
									 }
				   return title;
			 },
			deleimg(index){
				
				this.imglist.splice(index,1);
				var length=this.imglist.length;
				this.tslength=length;
				console.log(length);
				if(length==0){
				this.imglist.splice(0,1);
					var starobj={
					 path:'',
					 imgtrue:false,
					 imgtext:"添加照片"
					};
				 this.imglist.push(starobj);	
				}
				
			 },
			 scimgs(){
				 var that=this;
				 console.log(that.tslength);
				 uni.chooseImage({
				 	count:5-that.tslength,
					sizeType:"compressed",
					success:function(res){
					 var ts=res.tempFiles;
					  	for(var i=0; i<ts.length;i++){
					  		ts[i].imgtrue=true;
					  	}
					  that.imglist.push(...ts);	
					  that.tslength=that.imglist.length;
					},
					
				 })
			 },
			 scimg($event){       
			 				 var that=this;
						   if(that.tslength!=0){
							   var i=$event.currentTarget.dataset.id;
							   var url=that.imglist[i].path;
							   var urlarry=[];
							   urlarry.push(url);
							        uni.previewImage({
										urls:urlarry,
									})
						   }else{
			 				 uni.chooseImage({
			 				 	count:5,
			 					sizeType:"compressed",
			 					success:function(res){
			 				    /*that.imgtrue=true;*/		
							    var ts=res.tempFiles;
								that.tslength=res.tempFiles.length;	
								for(var i=0; i<ts.length;i++){
									ts[i].imgtrue=true;
								}	
								that.imglist.push(...ts);
								that.imglist.splice(0,1);
			 					}
			 				 })
							 }
			 },
			 scvideo(){
				    var that=this;
				           uni.chooseVideo({
				                           count: 1,
				                           sourceType: ['camera', 'album'],
				                           success: function (res) {
											   that.srctrue=true;
				                               that.src = res.tempFilePath;
				                           }
				                       });
			 },
			 nmchange(){
				 this.ok=!this.ok;
			  	 if(this.ok==true){
					 this.nmtitle="你写的评价会以匿名的形式展现"
				 }else{
					this.ok==false;
					this.nmtitle="公开的评价会展示在个人页面"
				 }
			 },
			 
		 }
		 
		}
	 
